import { Index } from "./index-model";
var index = new Index();
var app = getApp().globalData;
import wxcDialog from "../../../components/dialog/dialog";

export default {
  data() {
    return {
      remind: '加载中',
      angle: 0,
      userInfo: {
        avatarUrl: 'https://shelian.qtc369.com/images/20190821/2ecc3f99a443e5fb93124d3e0e0b3472.jpg'
      },
      logoUrl: ""
    };
  },

  components: {
    wxcDialog
  },
  props: {},
  onLoad: function (n) {
    wx.setNavigationBarTitle({
      title: `掌上嗨尔社联`
    });
    index.getLogo(res => {
      this.setData({
        logoUrl: res.data.img_url
      });
    });
  },
  onReady: function () {
    setTimeout(() => {
      this.setData({
        remind: ''
      });
    }, 1000);
    wx.onAccelerometerChange(res => {
      let angle = -(res.x * 30).toFixed(1);

      if (angle > 14) {
        angle = 14;
      } else if (angle < -14) {
        angle = -14;
      }

      if (this.angle !== angle) {
        this.setData({
          angle: angle
        });
      }
    });
  },
  onShow: function () {
    let userInfo = wx.getStorageSync('userInfo');
    

    if (!userInfo) {
      this.$refs.child.onchangeVisible(true);
    } else {
      this.setData({
        userInfo: userInfo
      });
      this.$refs.child.onchangeVisible(false);
    }
  },
  onHide: function () {},
  onUnload: function () {},
  onPullDownRefresh: function () {},
  onReachBottom: function () {},
  onShareAppMessage: function () {},
  methods: {
    goToIndex() {
      wx.switchTab({
        url: '../home/home'
      });
    },

    onConfirm(e) {
      // 点击允许
      
      let userInfo = JSON.parse(e.detail.rawData);

      if (!userInfo) {
        return;
      } else {
        console.log(userInfo);
      }

      this.setData({
        userInfo: userInfo
      });
      wx.setStorageSync('userInfo', userInfo);
	  this.$refs.child.onchangeVisible(false);
    },

    onCancel() {
      // 点击拒绝
      this.$refs.child.onchangeVisible(false);
    }

  }
};
